import './App.css';
import { useState, useCallback } from "react";
import data from "./data";
import ReactMarkdown from 'react-markdown';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { coldarkDark } from 'react-syntax-highlighter/dist/esm/styles/prism';
import remarkGfm from 'remark-gfm';
import rehypeRaw from 'rehype-raw';
import RightArrow from "./components/right_arrow";
import DownArrow from "./components/down_arrow";

function debounce(fn, wait, immediate) {
  let timeout;

  return (...args) => {
    const context = this;

    const later = () => {
      timeout = null;
      if (!immediate) fn.apply(context, args);
    };

    const callNow = immediate && !timeout;
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);

    if (callNow) {
      fn.apply(context, args);
    }
  };
}

function filterData(data, filter = "", typeFilter = "all") {
  
  if (typeFilter === "all" && filter.length) return data;

  const typeFilteredItems = {};
  typeFilteredItems.items = typeFilter !== "all" ? data.items.filter(item => item.type === typeFilter) : data.items;

  if (filter.length) return typeFilteredItems;

  const filtered = {};
  filtered.items = filter.length ? typeFilteredItems.items.filter(item => {
    return item.title.toLowerCase().includes(filter.toLowerCase()) || item.content.toLowerCase().includes(filter.toLowerCase());
  }) : typeFilteredItems.items;

  return filtered;
};

function App() {
  const [filter, setFilter] = useState("");
  const [typeFilter, setTypeFilter] = useState("all");
  const [expandedItems, setExpandedItems] = useState([]);
  const [expandedCategories, setExpandedCategories] = useState([]);

  function handleFilter(event) {
    debounce(setFilter(event.target.value), 300);
  }

  const handleExpandItem = useCallback((event) => {
    const newExpanded = expandedItems.includes(event) ? expandedItems.filter(i => i !== event) : [...expandedItems, event];
    setExpandedItems(newExpanded);
  }, [expandedItems]);

  const handleCategoryClick = useCallback((event) => {
    const newExpandedCategory = expandedCategories.includes(event) ? expandedCategories.filter(i => i !== event) : [...expandedCategories, event];
    setExpandedCategories(newExpandedCategory);
  }, [expandedCategories]);

  function expandAll() {
    setExpandedCategories(data.categories.map(category => category.title));
  }

  function collapseAll() {
    setExpandedItems([]);
    setExpandedCategories([]);
  }

  function clearFilters() {
    setFilter("");
    setTypeFilter("all");
  }

  const filtered = filter.length || typeFilter !== "all" ? filterData(data, filter, typeFilter) : data;

  return (
    <div className="App">
      <div className={"inputFilter"}>
        <span align="center"><input type="text" onChange={handleFilter} value={filter} /><button onClick={clearFilters}>Clear Search</button></span>
        <span align="center">           <a href="https://github.com/tteck/Proxmox/blob/main/LICENSE"><img alt="License MIT" src="https://img.shields.io/badge/license-MIT-blue" /></a> <a href="https://github.com/tteck/Proxmox/discussions"><img src="https://img.shields.io/badge/%F0%9F%92%AC-Discussions-teal" alt="Discussions" /></a> <a href="https://github.com/tteck/Proxmox/blob/main/CHANGELOG.md"><img src="https://img.shields.io/badge/-Changelog-maroon" alt="Changelog" /></a> <a href="https://github.com/tteck/Proxmox/blob/main/USER_SUBMITTED_GUIDES.md"><img src="https://img.shields.io/badge/-Guides-navy" alt="Guides" /></a> <a href="https://ko-fi.com/D1D7EP4GF"><img src="https://img.shields.io/badge/%E2%98%95-Buy%20me%20a%20coffee-brown" alt="Buy me a coffee" /></a></span>
      </div>
      {filtered.items.length === 0 && <div>No items match your criteria <button onClick={clearFilters}>Clear Filters</button></div>}
      <div><span onClick={expandAll}>      </span>   <span onClick={collapseAll}>     </span></div>
      {filtered.items?.map((item, index) => {
        return <div className={"App-items"} key={item.title} >
          {(index === 0 || filtered.items[index === 0 ? 0 : index - 1].category !== item.category) && <div onClick={() => handleCategoryClick(item.category)} className={"itemCategory"}>
            {item.category}{(((filter && item.title.toLowerCase().includes(filter.toLowerCase())) || expandedItems.includes(item.title)) || expandedCategories.includes(item.category)) ? <DownArrow className="navArrow" alt="Expanded" /> : <RightArrow className="navArrow" alt="Collapsed" />} </div>}

          {(((filter && item.title.toLowerCase().includes(filter.toLowerCase())) || expandedItems.includes(item.title)) || expandedCategories.includes(item.category)) && <span onClick={() => handleExpandItem(item.title)} className={"itemTitle"}>{item.title}</span>}

          {
            ((filter && item.title.toLowerCase().includes(filter.toLowerCase())) || expandedItems.includes(item.title)) && <div className="item">
              <ReactMarkdown
                children={item.content}
                remarkPlugins={[remarkGfm]}
                rehypePlugins={[rehypeRaw]}
                components={{
                  code({ node, inline, className, children, ...props }) {
                    const match = /language-(\w+)/.exec(className || '');
                    return !inline && match ? (
                      <>
                        <CopyButton valueToCopy={String(children)} />
                        <SyntaxHighlighter
                          children={String(children).replace(/\n$/, '')}
                          style={coldarkDark}
                          language={match[1]}
                          PreTag="div"
                          {...props}
                        />
                      </>
                    ) : (
                      <code className={className} {...props}>
                        {children}
                      </code>
                    );
                  }
                }}
              />
            </div>}
        </div>;
      })}
    </div>
  );
}

function CopyButton({ valueToCopy }) {
  const [copied, setCopied] = useState(false);

  function handleClick(text) {
    navigator.clipboard.writeText(text);
    setCopied(true);

    setTimeout(() => {
      setCopied(false);
    }, 2000);
  }

  return (
    <button
      className={`copy-button ${copied ? 'copied' : ''}`}
      onClick={() => handleClick(valueToCopy)}
    >
      {copied ? 'Copied!' : 'Copy'}
    </button>
  );
}

export default App;

